.newUser{
    flex: 4;
    margin-left: 2rem;
}
.newUserForm{
    display: flex;
    flex-wrap: wrap;
}
.newUserItem{
    width: 400px;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-right: 20px;
}
.newUserItem>label{
    font-weight: 600;
    font-size: 14px;
    margin-bottom: 10px;
    color: gray;
}
.newUserItem>input{
height: 20px;
padding: 10px;
border: 1px solid gray;
border-radius: 5px;
}
.newUserGender>input{
    margin-top: 15px;
}
.newUserGender>label{
margin: 10px;
font-size: 18px;
color: #555;
}
.newUserSelect{
    height: 40px;
    border-radius: 5px;
}
.newUserButton{
    font-weight: 600;
    background-color: darkblue;
    width: 200px;
    border: none;
    color: white;
    padding: 7px 10px;
    border-radius: 10px;
    margin-top: 30px;
    cursor: pointer;
}